import Vue from 'vue'
import Router from 'vue-router'
import Bill from '@/components/Bill'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Bill',
      name: 'Bill',
      component: Bill
    },
    {
      path: '/',
      name: 'Bill',
      component: Bill
    }
  ]
})



