<template>
  <div class="Bill">
      <h2>鄢家银-饭卡管理系统</h2>
    <div>
      <div>
        <textarea rows="25px" cols="70px" v-model="record" placeholder="报账记录。。。"></textarea>
      </div>
      <div>

        （非必填，默认上次）全量用户：<input type="file" ref="clearFile" @change="fileUser"></br>
        【注意：内部缓存用户列表，如果用户缺少，上传新的完整用户即可刷新缓存】
      </div>
      <div>
        <button @click="getExcel">一键导出</button>
      </div>
    </div>
<!--    <div>
      <table>
        <thead>
        <tr>
          <th>姓名</th>
          <th>金额</th>
          <th>余额</th>
          <th>下一位</th>
        </tr>
        </thead>
        <tbody id="tbody">
          <tr v-for="item of list">
            <td>{{item.name}}</td>
            <td>{{item.money}}</td>
            <td>{{item.balance}}</td>
            <td>{{item.next}}</td>
          </tr>
        </tbody>
      </table>
    </div>-->
  </div>
</template>

<script>
export default {
  data () {
    return {
      list:[{name:"张三",money:7.5,balance:300,next:"李四"}],
      record:"",
      userModalFile:null //文件改变的时候拿到excel
    }
  },
  methods:{
   /* clearFile(){
      this.$refs.clearFile.value = '';
    },*/
    fileUser(ele){
      //console.log(ele)

      this.userModalFile = ele.target.files[0];
      //console.log(this.userModalFile)
    },
    getExcel(){
      if(this.record==null){
        alert("记账记录不能为空");
        return;
      }
      let formData = new FormData();
      //把文件信息放入对象中
      formData.append("user", this.userModalFile);
      formData.append("record", this.record);
      this.$axios.post(this.baseUrl+"/bill/usertxt",formData,{
        responseType: "blob"
      }).then(function(res) {
         //console.log(res)
        // 处理返回的文件流
        const content = res.data;
        const blob = new Blob([content]);
        var date =
          new Date().getFullYear() +
          "" +
          (new Date().getMonth() + 1) +
          "" +
          new Date().getDate();
        const fileName = date + name + ".xlsx";
        if ("download" in document.createElement("a")) {
          // 非IE下载
          const elink = document.createElement("a");
          elink.download = fileName;
          elink.style.display = "none";
          elink.href = URL.createObjectURL(blob);
          document.body.appendChild(elink);
          elink.click();
          URL.revokeObjectURL(elink.href); // 释放URL 对象
          document.body.removeChild(elink);
        } else {
          // IE10+下载
          navigator.msSaveBlob(blob, fileName);
        }

      }).catch((err)=>{
        console.log(err)
        alert(err);
      })

    }
  }
}
</script>

<style scoped>
  table{
    border: solid 2px;
    border-collapse:collapse;
    margin: auto;
    width: 300px;
  }
  td,th{
    border: solid 1px;
  }
</style>



